<template>
  <Modal id="newUser" :hasFooter="true" title="新增用戶">
    <template v-slot:body>
      <div class="mb-3">
        <label for="add_mail" class="form-label">Email</label>
        <input type="email" class="form-control" id="add_mail" v-model="account" required>
      </div>
      <div class="mb-3">
        <label for="add_password" class="form-label">密碼</label>
        <input type="password" class="form-control" id="add_password" v-model="password" required
        pattern="^(?=.*\\d)(?=.*[a-z])(?=.*[A-Z]).{8,16}$">
        <small class="text-muted">最短 8 字元，且必須含有英文大小寫與數字混合</small>
      </div>
    </template>
    <template v-slot:footer>
      <button type="button" class="btn btn-primary" @click="registerUser">建立</button>
    </template>
  </Modal>
</template>

<script>
import { ref, getCurrentInstance } from 'vue';
import Modal from '@/components/Modal.vue';

export default {
  name: 'CreareUsers',
  components: {
    Modal,
  },
  emits: ['create-user-method'],
  setup(props, { emit }) {
    const { proxy } = getCurrentInstance();
    const account = ref('');
    const password = ref('');
    function registerUser() {
      proxy.axios
        .post('/v1/user/register', {
          account: account.value,
          password: password.value,
          nickname: account.value.split('@')[0],
          roles: [1],
        })
        .then((response) => {
          account.value = '';
          password.value = '';
          if (response.data.message === '成功') {
            emit('createUserMethod', { target: 'import', type: '成功' });
          } else {
            emit('createUserMethod', { target: 'import', type: '失敗', error: response.data.message });
          }
        }).catch((error) => {
          account.value = '';
          password.value = '';
          emit('createUserMethod', { target: 'import', type: '失敗', error: `${error.response.data.statusCode}:${error.response.data.message}` });
        });
    }
    return {
      registerUser,
      account,
      password,
    };
  },
};
</script>
