<template>
  <Modal id="newUserByCsv" :hasFooter="true" title="新增用戶">
  <template v-slot:body>
    <div class="mb-3" v-if="!showResult">
      <label for="add_csv" class="form-label">批次新增用戶 CSV 檔案</label>
      <input type="file" class="form-control" id="add_csv" @change="onFileChange">
    </div>
    <div v-if="!showResult">
      <p>格式範例：</p>
      <div class="p-3 bg-light">
        <p class="mb-0 text-muted"><small>暱稱,Email(帳號),權限</small></p>
        <p class="mb-0 text-muted">test-csv,testcsv1234@hexschool.com,student</p>
      </div>
    </div>
    <div v-else>
      <p class="mb-1 h5">匯入結果：</p>
      <p class="mb-0"><small>共匯入 {{ results.total_user }} 位使用者</small></p>
      <p class="mb-1">
        <small>
        {{ results.success_user_count }} 位成功｜
        {{ results.total_user - results.success_user_count }} 位失敗
        </small>
      </p>
      <div v-if="results.success_user.length > 0"
        class="p-3" style="background-color: rgba(25,135,84,0.1);">
        <p class="mb-1 text-success"><small>[成功] 成功的使用者列表：</small></p>
        <div class="mb-0 text-success p-1 border-bottom small"
        v-for="(success, index) in results.success_user"
        :key="'f' + index + success.index">
        <p class="mb-0">暱稱：{{ success.nickname }}<br/>
          Email: {{ success.email }}</p></div>
      </div>
      <div v-if="results.failed_user.length > 0"
      class="p-3 bg-light mt-2">
        <p class="mb-1 text-muted"><small>[失敗] 失敗的使用者列表：</small></p>
        <div class="mb-0 text-muted p-1 border-bottom small"
        v-for="(fail, index) in results.failed_user"
        :key="'f' + index + fail.index">
        <p class="mb-0">暱稱：{{ fail.nickname }}<br/>
          Email: {{ fail.email }} <br>
          <span class="material-icons align-middle small">
            info
          </span> 失敗原因：{{ fail.reason }}</p> </div>
      </div>
      <div v-if="results.exist_user.length > 0"
      class="p-3 bg-light mt-2">
        <p class="mb-1 text-muted"><small>[失敗] 已存在的使用者列表：</small></p>
        <div class="mb-0 text-muted p-1 border-bottom small"
        v-for="(fail, index) in results.exist_user"
        :key="'f' + index + fail.index">
        <p class="mb-0">暱稱：{{ fail.nickname }}<br/>
          Email: {{ fail.email }} <br>
          <span class="material-icons align-middle small">
            info
          </span> 失敗原因：{{ fail.reason }}</p> </div>
      </div>
    </div>
    </template>
    <template v-slot:footer>
      <button v-if="!showResult" type="button" class="btn btn-primary"
      @click="registerUsers">建立</button>
      <button v-else type="button" class="btn btn-secondary" @click="closeModal">關閉</button>
    </template>
  </Modal>
</template>

<script>
import {
  ref, getCurrentInstance, reactive, toRefs,
} from 'vue';
import Modal from '@/components/Modal.vue';

export default {
  name: 'CreareUsers',
  components: {
    Modal,
  },
  emits: ['create-user-method'],
  setup(props, { emit }) {
    const { proxy } = getCurrentInstance();
    const showResult = ref(false);
    const data = reactive({
      results: {},
    });
    let formData = new FormData();
    function onFileChange(e) {
      formData = new FormData();
      formData.append('list', e.target.files[0]);
    }
    function registerUsers() {
      proxy.axios
        .post('/v1/user/registers', formData)
        .then((response) => {
          showResult.value = true;
          document.getElementById('add_csv').value = '';
          document.querySelector('#newUserByCsv .modal-dismiss').classList.add('d-none');
          data.results = response.data;
          if (response.data.message === '成功') {
            emit('createUserMethod', { target: '', type: '成功' });
          } else {
            emit('createUserMethod', { target: '', type: '失敗', error: response.data.message });
          }
        }).catch((error) => {
          emit('createUserMethod', { target: '', type: '失敗', error });
        });
    }
    function closeModal() {
      showResult.value = false;
      document.querySelector('#newUserByCsv .modal-dismiss').classList.remove('d-none');
      emit('createUserMethod', { target: 'csv', type: '' });
    }
    return {
      registerUsers,
      onFileChange,
      closeModal,
      showResult,
      ...toRefs(data),
    };
  },
};
</script>
