<template>
  <Modal id="alertModal" :hasFooter="true"
  :title="ModalType == 'deactivated' ?'停用帳戶' : '重發驗證信'"
  :classList="ModalType == 'resetPassword'? '' : 'bg-danger text-white'">
  <template v-slot:body>
    <template v-if="ModalType == 'resetPassword'">
      <!-- 重發驗證信 -->
      <p>確定要重新發送驗證信給 <b> {{ UserName }} </b> 這個用戶嗎？</p>
    </template>
    <template v-if="ModalType == 'deactivated'">
      <!-- 停用帳戶 -->
      <p>確認要停用 <b> {{ UserName }} </b> 這個用戶嗎？</p>
      <div class="my-2">
      <input type="text" class="form-control" id="deactivated"
        v-model="deactivatedName" placeholder="請輸入使用者暱稱（上面粗體文字）">
      </div>
    </template>
  </template>
  <template v-slot:footer>
    <template v-if="ModalType == 'resetPassword'">
      <button type="button" class="btn btn-secondary"
        @click="this.$emit('comfirm', 'resetPassword')">確認發重設密碼信</button>
    </template>
    <template v-if="ModalType == 'deactivated'">
      <button :disabled="deactivatedName !== UserName"
        type="button" class="btn btn-danger"
        @click="this.$emit('comfirm', 'deactivated')">停用帳戶</button>
    </template>
  </template>
  </Modal>
</template>

<script>
import { ref, computed, onMounted } from 'vue';
import Modal from '@/components/Modal.vue';

export default {
  name: 'AlertModal',
  components: {
    Modal,
  },
  props: {
    name: String, // User 暱稱
    type: String, // resetPassword: 重發驗證信 | deactivated: 停用帳號
  },
  setup(props) {
    const deactivatedName = ref('');
    const ModalType = computed(() => props.type);
    const UserName = computed(() => props.name);
    onMounted(() => {
      document.getElementById('alertModal')
        .addEventListener('hidden.bs.modal', () => {
          deactivatedName.value = '';
        });
    });
    return {
      deactivatedName,
      ModalType,
      UserName,
    };
  },
};
</script>
