import { createRouter, createWebHashHistory } from 'vue-router';
import getCookie from '../service/getCookie';
import Dashboard from '../views/Dashboard.vue';
import Login from '../views/Login.vue';
import Users from '../views/Users/Users.vue';
import UserList from '../views/Users/UserList.vue';
import Roles from '../views/Roles/Roles.vue';
import RolesList from '../views/Roles/RolesList.vue';

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/login/:timestamp',
    name: 'LoginVertify',
    component: () => import('../views/LoginVertify.vue'),
  },
  {
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard,
    children: [
      {
        path: 'users',
        name: 'Users',
        component: Users,
        children: [
          {
            path: '',
            name: 'UserList',
            component: UserList,
          },
          {
            path: ':id',
            name: 'AboutID',
            component: () => import('../views/Users/UserDetail.vue'),
          },
        ],
      },
      {
        path: 'permission/roles',
        name: 'Roles',
        component: Roles,
        children: [
          {
            path: '',
            name: 'RolesList',
            component: RolesList,
          },
          {
            path: 'create',
            name: 'CreateRole',
            component: () => import('../views/Roles/CreateRole.vue'),
          },
          {
            path: ':id',
            name: 'EditRole',
            component: () => import('../views/Roles/CreateRole.vue'),
          },
        ],
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

const isAuthenticated = getCookie('h_MqhC49RxFRI6pR3Z_token');
router.beforeEach((to, from, next) => {
  if (!(to.name === 'Login' || to.name === 'LoginVertify') && !isAuthenticated) {
    if (process.env.NODE_ENV === 'production') {
      window.location.href = `${process.env.VUE_APP_USER_URL}/login`;
    } else {
      next({ name: 'Login' });
    }
  } else if (to.path === '/' && isAuthenticated) {
    next('/admin/users');
  } else {
    next();
  }
});

export default router;
