<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <div class="card shadow-sm rounded-4 overflow-hidden mt-5">
          <div class="card-header bg-white d-flex justify-content-between align-items-center py-3">
            <h5 class="mb-0">登入系統</h5>
          </div>
          <div class="card-body display-12 fw-normal">
            <div class="mb-3">
            <label for="add_mail" class="form-label">Email</label>
            <input type="email" class="form-control" id="add_mail" v-model="account">
            </div>
            <div class="mb-3">
              <label for="add_password" class="form-label">密碼</label>
              <input type="password" class="form-control" id="add_password" v-model="password">
              <small class="text-muted">最短 8 字元，且必須含有英文大小寫與數字混合</small>
            </div>
            <div class="d-flex justify-content-end">
              <button class="btn btn-primary" @click="login">登入</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ref, getCurrentInstance } from 'vue';
import { useRouter } from 'vue-router';

export default {
  name: 'Login',
  setup() {
    const router = useRouter();
    const { proxy } = getCurrentInstance();
    const account = ref('');
    const password = ref('');

    function errorHint(msg) {
      const msgData = {
        statusBg: 'bg-danger',
        statusColor: ' text-white',
        title: 'Login Error',
        message: msg,
      };
      proxy.$bus.emit('push:message', msgData);
    }
    function login() {
      proxy.axios.post('/v1/user/login', {
        account: account.value,
        password: password.value,
      })
        .then((response) => {
          if (response.data.message === '成功') {
            window.sessionStorage.setItem('H_temp_mail_store', account.value);
            const tempstamp = new Date().getTime();
            router.push(`/login/${tempstamp}`);
          } else {
            errorHint(response.data.message);
          }
        }).catch((error) => {
          errorHint(error);
        });
    }
    return {
      account,
      password,
      login,
    };
  },
};
</script>
