import axios from 'axios';
import getCookie from './getCookie';

function generateRandomString(num) {
  const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let result1 = ' ';
  const charactersLength = characters.length;
  for (let i = 0; i < num; i += 1) {
    result1 += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result1;
}

axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? process.env.VUE_APP_API : '';
document.cookie = `xsrf-token=${generateRandomString(32)};`;
axios.defaults.withCredentials = true;
axios.defaults.xsrfHeaderName = 'xsrf-token';
axios.defaults.xsrfCookieName = 'xsrf-token';

const token = getCookie('h_MqhC49RxFRI6pR3Z_token');
axios.defaults.headers.common.Authorization = `Bearer ${token}`;
