<template>
    <div class="sidebar vh-100 position-fixed top-0 py-3 border-end z-1039 bg-white"
    :class="{'toggled': sideBarOpen}">
      <a href="#" class="d-md-none d-block"
        @click.prevent="$emit('update:sideBarOpen', false)">
        <span class="material-icons"> close </span>
      </a>
      <img src="https://rpg.hexschool.com/img/backend/brand/logo-rpg.svg" alt=""
      style="height: 30px;" class="img-fluid px-3">
      <!-- 側邊選單欄 -->
      <div class="overflow-y-auto mt-3">
        <div v-for="menu in sideBarMenu" :key="menu.id">
          <a v-if="menu.hasChild" data-bs-toggle="collapse"
            class="d-block text-dark text-decoration-none"
            :href="'#' + menu.id" role="button">
            <div class="d-flex justify-content-between px-4">
              <p class="mb-0">
                <span class="material-icons align-bottom me-2"> {{ menu.icon }} </span>
                {{ menu.title }}
              </p>
              <span class="material-icons align-middle">
                keyboard_arrow_down
              </span>
            </div>
          </a>
          <router-link class="d-block text-dark text-decoration-none px-4"
            v-else :to="menu.router">
            <p class="mb-0">
              <span class="material-icons align-bottom me-2"> {{ menu.icon }} </span>
              {{ menu.title }}
            </p>
          </router-link>
          <div class="collapse" :id="menu.id" v-if="menu.hasChild">
            <router-link v-for="child in menu.child" :key="child.id"
              class="d-block ps-4 text-dark text-decoration-none" :to="child.router">
              {{ child.title }}
            </router-link>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import {
  reactive, toRefs, onMounted, ref,
} from 'vue';
import { useRoute } from 'vue-router';
import { Collapse } from 'bootstrap';

export default {
  name: 'SideBar',
  emits: ['update:sideBarOpen'],
  props: {
    sideBarOpen: Boolean,
  },
  setup() {
    const router = useRoute();
    const data = reactive({
      sideBarMenu: [
        {
          id: 'user-manager',
          title: '用戶管理系統',
          icon: 'group',
          hasChild: true,
          child: [
            {
              id: 'all-user',
              title: '使用者管理',
              router: '/admin/users',
            },
            {
              id: 'user-do',
              title: '角色管理',
              router: '/admin/permission/roles',
            },
          ],
        },
        {
          id: 'assistant',
          title: '助教系統',
          icon: 'cast_for_education',
          router: '/admin/assistant/',
          hasChild: false,
        },
      ],
    });
    const collapseTarget = ref('');
    onMounted(() => {
      collapseTarget.value = new Collapse(document.querySelector('#user-manager'));
    });
    onMounted(() => {
      if (router.path.includes('users') || router.path.includes('permission')) {
        collapseTarget.value.show();
      }
    });
    return {
      ...toRefs(data),
    };
  },
};
</script>
