<template>
  <div class="roles p-4 position-relative">
    <loading :active="isLoading" :is-full-page="fullPage"></loading>
    <div class="d-flex justify-content-between align-items-center">
      <h5 class="mb-0">角色列表</h5>
      <router-link to="/admin/permission/roles/create"
        class="btn btn-sm btn-primary"> 新增角色 </router-link>
    </div>
    <div class="card shadow-sm rounded-4 overflow-hidden my-4"
      v-for="role in roleList" :key="role.id">
      <div class="card-body">
        <div class="d-flex align-items-start justify-content-between">
          <div>
            <p class="text-primary fw-bold mb-1">
              # {{ role.cht_name }}
            </p>
            <p class="text-muted">{{ role.description }}</p>
            <a :href="'#role' + role.id" class="d-inline-block btn btn-sm btn-outline-secondary
              rounded text-decoration-none display-12 fw-bold"
              data-bs-toggle="collapse" role="button" >
              展開細節
              <span class="material-icons align-middle display-12">
              expand
              </span>
            </a>
          </div>
          <div v-if="roleList && roleList.length > 0">
            <router-link :to="{
              name: 'EditRole',
              params: { id: role.id, roleSettings: JSON.stringify(role) }}"
              class="btn btn-secondary px-5">調整</router-link>
          </div>
        </div>
        <div class="collapse" :id="'role' + role.id">
          <div class="p-3">
            <ul v-if="role.ranges
              && role.ranges.length > 0
              && role.ranges[0]" class="list-unstyled mb-0">
              <li v-for="(range, index) in role.ranges"
                :key="'range' + index" class="mt-2">
                <p class="mb-1 fw-bold">
                  {{ range.cht_name }}
                </p>
                <p class="mb-0" v-for="(sub, index) in range.sub_ranges"
                :key="'sub'+ sub.id + index">
                  <small>- {{ sub.cht_name }}</small>
                </p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ref, reactive, toRefs, getCurrentInstance, onMounted,
} from 'vue';
import Loading from 'vue3-loading-overlay';
import 'vue3-loading-overlay/dist/vue3-loading-overlay.css';

export default {
  name: 'RolesList',
  components: {
    Loading,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const isLoading = ref(false);
    const fullPage = ref(true);
    const data = reactive({
      roleList: [],
    });
    function getData() {
      isLoading.value = true;
      proxy.axios
        .get('/v1/permission/roles')
        .then((response) => {
          data.roleList = response.data.roles;
          isLoading.value = false;
        });
    }
    onMounted(() => {
      getData();
    });
    return {
      fullPage,
      isLoading,
      ...toRefs(data),
    };
  },
};

</script>
