<template>
  <div class="about px-4 mt-4 position-relative">
    <loading :active="isLoading" :is-full-page="fullPage"></loading>
    <div class="card shadow-sm rounded-4 overflow-hidden mb-4">
      <div class="card-header bg-white py-3">
        <div class="row">
          <div class="col-md-6 d-flex align-items-center flex-lg-nowrap flex-wrap">
            <!-- <div class="form-check form-check-inline text-nowrap">
              <input class="form-check-input" type="radio"
              name="searchType" id="s_nickname" value="nickname"
              v-model="searchType">
              <label class="form-check-label" for="s_nickname">
                暱稱
              </label>
            </div>
            <div class="form-check form-check-inline text-nowrap">
              <input class="form-check-input" type="radio"
              name="searchType" id="s_email" value="email" v-model="searchType">
              <label class="form-check-label" for="s_email">email</label>
            </div> -->
            <div class="input-group">
              <input type="text" class="form-control" placeholder="搜尋 Email"
              v-model="searchText" @keyup.enter="getData(1)">
              <button class="btn btn-outline-secondary"
                type="button" id="search" @click="getData(1)">搜尋</button>
            </div>
          </div>
          <div class="col-md-6 d-flex justify-content-end mt-md-0 mt-3">
            <button class="btn btn-sm btn-outline-dark me-2"
            @click="OpenAddUserModal('csv')">批次新增用戶</button>
            <button class="btn btn-sm btn-primary" @click="OpenAddUserModal('single')">新增用戶</button>
          </div>
        </div>
      </div>
      <div class="card-body p-0">
        <div class="table-responsive">
          <table class="table table-hover mb-0 text-nowrap" style="font-size: 14px;">
            <thead class="bg-light">
              <tr>
                <th scope="col" class="ps-4 py-3 fw-normal border-0">姓名</th>
                <th scope="col" class="py-3 fw-normal border-0">Email</th>
                <th scope="col" class="py-3 fw-normal border-0">驗證狀態</th>
                <th scope="col" class="py-3 fw-normal border-0">最近登入日期</th>
                <th scope="col" class="py-3 fw-normal border-0">登入方式</th>
                <th scope="col" class="py-3 fw-normal text-end pe-3 border-0">編輯</th>
              </tr>
            </thead>
            <tbody class="text-nowrap">
              <tr class="align-middle" v-for="user in userList" :key="user.user_id">
                <td scope="row" class="ps-4">{{ user.nickname }}</td>
                <td>{{ user.email }}</td>
                <td>
                  <div v-html="statusCheck(user.state_cht_name)"></div>
                </td>
                <td>{{ user.last_login_time?
                  user.last_login_time.split('T')[0]: '' }}</td>
                <td>{{ user.login_type }}</td>
                <td class="text-end pe-3">
                  <router-link :to="'/admin/users/' + user.user_id"
                    class="btn btn-sm btn-outline-dark me-2">
                    編輯 <span class="material-icons align-middle fs-6"> edit </span></router-link>
                  <button class="btn btn-sm btn-outline-dark dropdown-toggle"
                    type="button" data-bs-toggle="dropdown" aria-expanded="false">
                    更多
                  </button>
                  <ul class="dropdown-menu">
                    <li>
                      <button type="button" class="dropdown-item"
                        @click="OpenAlertModal('resetPassword', user.nickname, user.user_id)">
                        發重設密碼信</button></li>
                    <li>
                      <button class="dropdown-item"
                      @click="OpenAlertModal('deactivated', user.nickname, user.user_id)"
                      >停用帳戶</button></li>
                  </ul>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="card-footer bg-white py-3">
        <nav aria-label="Page navigation example">
          <ul class="pagination mb-0">
            <li class="page-item">
              <a class="page-link" href="#" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li class="page-item"
            v-for="(page, index) in pagination.total_page"
            :key="'user_p' + index"
            :class="{'active': pagination.current_page == page}">
              <a class="page-link" href="#" @click.prevent="getData(page)">{{ page }}</a>
            </li>
            <li class="page-item">
              <a class="page-link" href="#" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
    <CreateUser @create-user-method="createUserMethod"></CreateUser>
    <CreateUserByCsv @create-user-method="createUserMethod"></CreateUserByCsv>
    <AlertModal :type="alertModalType" :name="alertModalUserName"
    @comfirm="comfirmAlertModal"></AlertModal>
  </div>
</template>

<script>
import {
  ref, reactive, toRefs, onMounted, getCurrentInstance,
} from 'vue';
import { Modal } from 'bootstrap';
import Loading from 'vue3-loading-overlay';
import CreateUser from '../../components/Users/Users/CreateUser.vue';
import CreateUserByCsv from '../../components/Users/Users/CreateUserByCsv.vue';
import AlertModal from '../../components/Users/Users/AlertModal.vue';
import 'vue3-loading-overlay/dist/vue3-loading-overlay.css';
import mixinPostMsg from '../../service/mixinPostMsg';

export default {
  name: 'UserList',
  components: {
    CreateUser,
    CreateUserByCsv,
    AlertModal,
    Loading,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const isLoading = ref(false);
    const fullPage = ref(false);
    const modal = ref('');
    const csvModal = ref('');
    const alertModal = ref('');
    const { postToasts } = mixinPostMsg(); // 發送訊息
    const data = reactive({
      userList: [],
      pagination: {},
    });

    // 搜尋綁定，type: email => 目前可搜尋 mail
    const searchText = ref('');
    const searchType = ref('email');

    // 取得使用者列表：如果 searchText 有值就帶入到搜尋
    function getData(page = 1) {
      isLoading.value = true;
      let url = `/v1/user/list?count=10&page=${page}`;
      if (searchText.value !== '') {
        url = `/v1/user/list?count=10&page=${page}&filter=${searchType.value}&search_value=${searchText.value}`;
      }
      proxy.axios
        .get(url)
        .then((response) => {
          data.userList = response.data.user_list;
          data.pagination = response.data.pagination;
          isLoading.value = false;
        }).catch((error) => {
          isLoading.value = false;
          postToasts('danger', '取得用戶列表失敗', error);
        });
    }
    // 驗證狀態顯示
    function statusCheck(text) {
      if (text === '已驗證帳號') {
        return '<span class="material-icons text-primary">check_circle</span>';
      }
      if (text === '未驗證帳號') {
        return '';
      }
      return '<span class="material-icons-outlined text-danger">remove_circle</span>';
    }

    // 打開 建立使用者 和 批次建立使用者的 一般 Modal
    function OpenAddUserModal(type) {
      if (type === 'single') {
        modal.value.show();
      } else {
        csvModal.value.show();
      }
    }
    // 建立使用者 和 批次建立使用者 送出後的回傳訊息和 Modal 操作
    function createUserMethod(value) {
      if (value.type === '成功') {
        postToasts('success', '建立使用者成功', '已成功建立使用者');
      } else if (value.type === '失敗') {
        postToasts('danger', '建立使用者失敗', value.error);
      }
      if (value.target === 'import') {
        modal.value.hide();
        getData();
      }
      if (value.target === 'csv') {
        csvModal.value.hide();
        getData();
      }
    }

    // 下拉選單操作：重發驗證信、停用帳號 start
    // 打開 重發驗證信 和 的 停用帳戶 的 危險操作 Modal
    const alertModalType = ref('');
    const alertModalUserName = ref('');
    const alertModalUserID = ref(0);
    function OpenAlertModal(type, name, id) {
      alertModal.value.show();
      alertModalType.value = type;
      alertModalUserName.value = name;
      alertModalUserID.value = id;
    }
    // 重發驗證信
    function sendPasswordMail() {
      proxy.axios
        .post(`/v1/user/${alertModalUserID.value}/reset_password`)
        .then((response) => {
          if (response.data.message === '成功') {
            alertModal.value.hide();
            postToasts('success', '已寄出重設密碼信件', `${alertModalUserName.value} 的重設密碼信件已經成功寄出。`);
          } else {
            alertModal.value.hide();
          }
        }).catch((error) => {
          alertModal.value.hide();
          postToasts('danger', '重設密碼信件寄出失敗', error);
        });
    }
    // 停用帳戶
    function deactivatedAccount() {
      proxy.axios
        .put(`/v1/user/${alertModalUserID.value}/suspend`)
        .then((response) => {
          if (response.data.message === '成功') {
            alertModal.value.hide();
            postToasts('success', '停用帳戶成功', `${alertModalUserName.value} 已被停用。`);
          } else {
            alertModal.value.hide();
            postToasts('danger', '停用帳戶失敗', response.data.message);
          }
        }).catch((error) => {
          alertModal.value.hide();
          postToasts('danger', '停用帳戶失敗', error);
        });
    }
    // 在 AlertModal 點下 Comfirm 後的行為
    function comfirmAlertModal(value) {
      // value - resetPassword: 重發驗證信 | deactivated: 停用帳號
      if (value === 'resetPassword') {
        sendPasswordMail();
      } else if (value === 'deactivated') {
        deactivatedAccount();
      }
    }
    // 下拉選單操作：重發驗證信、停用帳號 end

    onMounted(() => {
      modal.value = new Modal(document.getElementById('newUser'));
      csvModal.value = new Modal(document.getElementById('newUserByCsv'));
      alertModal.value = new Modal(document.getElementById('alertModal'));
      getData();
    });
    return {
      isLoading,
      fullPage,
      OpenAddUserModal,
      OpenAlertModal,
      alertModalType,
      alertModalUserName,
      alertModalUserID,
      ...toRefs(data),
      getData,
      comfirmAlertModal,
      searchText,
      searchType,
      createUserMethod,
      statusCheck,
    };
  },
};
</script>
