<template>
  <div aria-live="polite" aria-atomic="true" class="position-relative">
    <div class="toast-container end-0 p-3"
      style="position: fixed; top: 15px; right: 15px;
      min-width: 180px;z-index: 1060;">
      <div v-for="msg in messages" :key="msg.id"
        class="toast show" role="alert" aria-live="assertive" aria-atomic="true">
        <div class="toast-header" :class="msg.toastMsg.statusBg">
          <strong class="mr-auto"
          :class="msg.toastMsg.statusColor">
            {{ msg.toastMsg.title }}
          </strong>
        </div>
        <div class="toast-body">
          {{ msg.toastMsg.message }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { Toast } from 'bootstrap';

export default {
  name: 'Toast',
  data() {
    return {
      messages: [],
      toastMsg: {
        statusBg: 'bg-secondary', // bg-success
        statusColor: 'text-white', // text-white
        title: '我是標題',
        message: '我是內文',
      },
    };
  },
  mounted() {
    this.$bus.on('push:message', (value) => {
      this.messages.push({
        id: new Date().getTime(),
        toastMsg: {
          ...this.toastMsg,
          ...value,
        },
      });
      setTimeout(() => {
        this.messages.shift();
      }, 8000);
    });
  },
};
</script>
